import React from "react";
import { Fragment } from "react";

export default function Producto({item, anadirACarrito}) {
  const { id, nombre, descripcion, precio, imagen } = item;



  return (
    <Fragment>
      <div class="col-md-4" >
        <div class="card mt-3">
          <div class="product-1 align-items-center p-2 text-center">
            <img src={imagen} alt="" class="rounded" width="80%" />
            <h5>{nombre}</h5>

            <div class="mt-3 info">
              <p class="text1 d-block">{descripcion}.</p>

              <div class="cost m-3 text-dark fw-bold">
                <span>S/{precio}</span>
              </div>
              <div>
                <button className="btn btn-info"> Comprar</button>
                <button className="btn btn-success mx-2 m-md-2" >
                  Agregar al Carrito
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
}
